import Vue from "vue";
import Vuex from "vuex";

import axios from "./axios-common.js";


Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    qnalist: [],
    qna: null,
  },
  getters: {
    qnalist(state) {
      return state.qnalist;
    },
    todo(state) {
      return state.qna;
    }
  },
  actions: {
    selectAll: (store) => {
      axios.get("/qna")
        .then((response) => {
          store.commit("SELECT_ALL", { qnalist: response.data })
          
        })
        .catch((response)=> {console.log(response);})
    },

    selectOne: (store,payload) => {
      console.log(payload);
      axios.get("/qna/" + payload.id)
      .then((response) => {
        console.log("조회 하였습니다.");
        store.commit("SELECT_ONE", { qna: response.data });
      })
        .catch((exp) => alert("조회 처리에 실패 했습니다." + exp))
      
    },
    addQna: (store, payload) => {
      axios
        .post("/qna/", payload )
        .then(() => {
          console.log("추가 처리하였습니다.");
          store.dispatch("selectAll"); //call action
          console.log(payload);
        })
        .catch((exp) => {
          alert("추가 처리에 실패하였습니다." + exp)
          console.log(payload);
      
        });
         
    },
    removeQna: (store, payload) => {
			axios
				.delete("/qna/" + payload)
				.then(() => {
					console.log("삭제 성공!");
          store.dispatch("selectAll");
				})
        .catch((exp) => {
         
          console.log("삭제하지 못했습니다" + exp);
				});
    },
    updateQna: (store, payload) => {
			axios
				.put("/qna" , payload)
				.then(() => {
					console.log("수정 처리하였습니다.");
					store.dispatch("selectAll"); //call action
				})
				.catch((exp) => alert("수정 처리에 실패하였습니다." + exp));
		},
  },
  mutations: {
    SELECT_ALL: (state, payload) =>{
      state.qnalist = payload.qnalist;
    },
    SELECT_ONE: (state, payload) => {
      state.qna = payload.qna;
    }
  },

});
