import Vue from "vue";
import VueRouter from "vue-router";

import QnaList from "../components/QnaList.vue";
import QnaDetail from "../components/QnaDetail.vue";
import QnaInput from "../components/QnaInput.vue"
import QnaUpdate from "../components/QnaUpdate.vue"
Vue.use(VueRouter);

const routes = [
  {
    path: "/qna",
    name: "QnaList",
    component: QnaList,
  },
  {
    path: "/qna/:id",
    name: "QnaDetail",

    component: QnaDetail,
  },
  {
    path: "/qnaInput",
    name: "QnaInput",

    component: QnaInput,
  },
  {
    path: "/qnaUpdate/:id",
    name: "QnaUpdate",
    component:QnaUpdate,
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
