<template>
	<div>
		<h1>게시판 상세보기</h1>

		<div class="AddWrap">
			<form>
				<table class="tbAdd">
					<colgroup>
						<col width="15%" />
						<col width="*" />
					</colgroup>
					<tr>
						<th>글 번호</th>
						<td>{{ qna.qid }}</td>
					</tr>
					<tr>
						<th>내용</th>
						<td class="txt_cont" v-html="qna.qque"></td>
					</tr>
				</table>
			</form>
		</div>
		<div class="btnWrap"><a @click="goUpdate(qna.qid)" class="btn">수정</a></div>
		<div class="btnWrap"><a @click="goList" class="btn">목록</a></div>
		<div class="btnWrap"><a @click="removeQna(qna.qid)" class="btn">삭제</a></div>
	</div>
</template>

<script>
export default {
	created() {
		var no = this.$route.params.id;
		console.log("detail" + no);
		this.$store.dispatch("selectOne", { id: no });
	},
	computed: {
		qna() {
			console.log("조회완료");
			return this.$store.state.qna;
		},
	},
	methods: {
		goList() {
			this.$router.push("/qna");
		},
		removeQna(id) {
			console.log(id);
			this.$store.dispatch("removeQna", id);
			this.$router.push("/qna");
		},
		goUpdate(id) {
			this.$router.push("/qnaUpdate/" + id);
		},
	},
};
</script>

<style scoped>
.tbAdd {
	border-top: 1px solid #888;
}
.tbAdd th,
.tbAdd td {
	border-bottom: 1px solid #eee;
	padding: 5px 0;
}
.tbAdd td {
	padding: 10px 10px;
	box-sizing: border-box;
	text-align: left;
}
.tbAdd td.txt_cont {
	height: 300px;
	vertical-align: top;
}
.btnWrap {
	text-align: center;
	display: inline-block;
	margin: 20px 0 0 0;
}
.btnWrap a {
	margin: 0 10px;
}
.btnAdd {
	background: #43b984;
}
.btnDelete {
	background: #f00;
}
</style>
