<template>
	<div>
		<table>
			<thead>
				<tr>
					<th>질문</th>
					<th>작성자</th>
					<th>시간</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="q in qnalist" :key="q.qid">
					<td align="center" @click="qnaDetail(q.qid)">{{ q.qque }}</td>
					<td align="center">{{ q.quserid }}</td>
					<td align="center">{{ q.qdate }}</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
	created() {
		this.$store.dispatch("selectAll"); //action call
	},
	computed: {
		...mapGetters(["qnalist"]), // store.js 파일안에 잇는 getter중에 qnalist
	},
	methods: {
		...mapActions({}),
		qnaDetail(id) {
			this.$router.push("/qna/" + id);
		},
	},
};
</script>
<style scoped>
.del {
	text-decoration: line-through;
}
table {
	padding-left: 20px;
	margin-top: 0;
	text-align: left;
}
th {
	min-height: 50px;
	height: 50px;
	line-height: 50px;
	margin: 0.5rem 0;
	padding: 0 0.9rem;
	background: white;
	border-radius: 5px;
}
.checkBtn {
	line-height: 45px;
	color: #62acde;
	margin-right: 5px;
}
.removeBtn {
	margin-left: auto;
	color: #de4343;
}

.list-enter-active,
.list-leave-active {
	transition: all 1s;
}
.list-enter,
.list-leave-to {
	opacity: 0;
	transform: translateY(30px);
}
</style>
